import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { IonicStorageModule } from '@ionic/storage';
import { InAppBrowser } from '@ionic-native/in-app-browser';

import { AboutPage } from '../pages/about/about';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';
import { SettingsPage } from '../pages/settings/settings';
import { TeamDataPage } from '../pages/team-data/team-data';
import { AddTeamPage } from '../pages/add-team/add-team';
import { EditTeamPage } from '../pages/edit-team/edit-team';
import { AddMatchPage } from '../pages/add-match/add-match';
import { SigninPage } from '../pages/signin/signin';
import { InitialTutorialPage } from '../pages/initial-tutorial/initial-tutorial';
import { CreditsPage } from '../pages/credits/credits';
import { PredictionsTabPage } from '../pages/predictions-tab/predictions-tab';
import { PredictionsPage } from '../pages/predictions/predictions';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { TeamsProvider } from '../providers/teams/teams';

@NgModule({
  declarations: [
    MyApp,
    AboutPage,
    HomePage,
    TabsPage,
    SettingsPage,
    TeamDataPage,
    AddTeamPage,
    EditTeamPage,
    AddMatchPage,
    SigninPage,
    InitialTutorialPage,
    CreditsPage,
    PredictionsTabPage,
    PredictionsPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/add-match/add-match.module.ngfactory#AddMatchPageModuleNgFactory', name: 'AddMatchPage', segment: 'add-match', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/credits/credits.module.ngfactory#CreditsPageModuleNgFactory', name: 'CreditsPage', segment: 'credits', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/edit-team/edit-team.module.ngfactory#EditTeamPageModuleNgFactory', name: 'EditTeamPage', segment: 'edit-team', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/add-team/add-team.module.ngfactory#AddTeamPageModuleNgFactory', name: 'AddTeamPage', segment: 'add-team', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/initial-tutorial/initial-tutorial.module.ngfactory#InitialTutorialPageModuleNgFactory', name: 'InitialTutorialPage', segment: 'initial-tutorial', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/predictions-tab/predictions-tab.module.ngfactory#PredictionsTabPageModuleNgFactory', name: 'PredictionsTabPage', segment: 'predictions-tab', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/predictions/predictions.module.ngfactory#PredictionsPageModuleNgFactory', name: 'PredictionsPage', segment: 'predictions', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/settings/settings.module.ngfactory#SettingsPageModuleNgFactory', name: 'SettingsPage', segment: 'settings', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/signin/signin.module.ngfactory#SigninPageModuleNgFactory', name: 'SigninPage', segment: 'signin', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/team-data/team-data.module.ngfactory#TeamDataPageModuleNgFactory', name: 'TeamDataPage', segment: 'team-data', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    AboutPage,
    HomePage,
    TabsPage,
    SettingsPage,
    TeamDataPage,
    AddTeamPage,
    EditTeamPage,
    AddMatchPage,
    SigninPage,
    InitialTutorialPage,
    CreditsPage,
    PredictionsTabPage,
    PredictionsPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    TeamsProvider,
    InAppBrowser
  ]
})
export class AppModule {}



